.filler {
  flex: 1;
}

.buttons {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.icon-button {
  --button-color: var(--inactive-color);
  min-width: 1.5em;
  min-height: 1.5em;
  color: var(--button-color);
  cursor: pointer;
  font: inherit;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: smaller;
  text-decoration: none;
  display: flex;
}

.icon-button:hover, .icon-button.hover, .title.hover > .buttons > .collapse-toggle {
  --button-color: var(--hover-color);
}

.icon-button:active, .icon-button.active, .title.active > .buttons > .collapse-toggle {
  --button-color: var(--active-color);
}

.icon-button > * {
  line-height: normal !important;
}

.icon-button.circular {
  border: 2px solid var(--button-color);
  border-radius: 50%;
}

.card {
  border: 2px solid var(--decoration-color);
  border-radius: 8px;
}

.card:hover {
  border-color: var(--decoration-hover-color);
}

.card > .title {
  border-bottom: 2px solid var(--border-color);
  padding: 5px 2px 5px 10px;
  transition-property: border-bottom;
  transition-delay: 0s;
}

.title {
  cursor: pointer;
  flex-direction: row;
  align-items: center;
  display: flex;
}

.title.collapsed {
  transition: border-bottom-width 0s var(--collapse-transition-time);
  border-bottom-width: 0;
}

.title > .text {
  font-weight: bolder;
}

.title > .buttons > .collapse-toggle {
  transition: transform .1s;
}

.title.collapsed > .buttons > .collapse-toggle {
  transform: rotate(-.5turn);
}

.content-wrapper {
  overflow: hidden;
}

.card > .content-wrapper > .content {
  padding: 10px;
}

.doc-button {
  position: relative;
}

.doc-button > .doc {
  clip-path: inset(0 0 100%);
  z-index: 1;
  background-color: var(--decoration-color);
  color: var(--color);
  width: max-content;
  max-width: min(300px, 75vw);
  transition: clip-path .3s;
  position: absolute;
  top: calc(100% + 15px);
  right: -100%;
}

.doc-button.active > .doc {
  clip-path: inset(0);
}

ul.icon-list {
  flex-flow: wrap;
  gap: 8px;
  margin: 0;
  padding: 0;
  display: flex;
}

ul.icon-list > li {
  border: 2px solid var(--decoration-color);
  border-radius: 8px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 3px 8px;
  display: flex;
}

ul.icon-list > li:hover {
  border-color: var(--decoration-hover-color);
  background-color: var(--decoration-color);
}

ul.icon-list > li > .icon {
  height: 1em;
}

.cv-table {
  border-collapse: collapse;
  width: 100%;
}

.cv-table tr {
  --row-border-color: var(--decoration-color);
  vertical-align: top;
  border: none;
}

.cv-table tr:hover {
  --row-border-color: var(--decoration-hover-color);
}

.cv-table th, .cv-table td {
  padding: 5px 10px;
}

.cv-table th:not(:first-child), td:not(:first-child) {
  border-left: 2px solid var(--row-border-color);
}

.cv-table ul, .cv-table ol {
  margin: 0;
}

.cv-table .text .prize {
  font-style: italic;
  font-weight: lighter;
}

#introduction .content {
  flex-direction: row;
  gap: 20px;
  display: flex;
}

@media screen and (width < 650px) {
  #introduction .content {
    flex-direction: column;
  }
}

#introduction .text > header {
  font-size: larger;
}

.profile {
  flex-direction: column;
  align-items: center;
  gap: 10px;
  display: flex;
}

.profile > .pic {
  border-radius: 50%;
  width: 150px;
  height: 150px;
}

.profile > .pic > * {
  clip-path: circle(45%);
  width: 100%;
  height: 100%;
}

.profile > .socials {
  flex-direction: row;
  justify-content: center;
  gap: 10px;
  font-size: 22px;
  display: flex;
}

#skills .content {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

#skills header {
  margin-bottom: 10px;
  text-decoration: underline;
}

#skills .doc .legend {
  color: var(--decoration-hover-color);
}

.app[data-theme="light"] #skills .high {
  --decoration-color: lightgreen;
  --decoration-hover-color: darkseagreen;
}

.app[data-theme="dark"] #skills .high {
  --decoration-color: darkgreen;
  --decoration-hover-color: darkseagreen;
}

.app[data-theme="light"] #skills .medium {
  --decoration-color: lightblue;
  --decoration-hover-color: skyblue;
}

.app[data-theme="dark"] #skills .medium {
  --decoration-color: royalblue;
  --decoration-hover-color: skyblue;
}

